import { Award } from "react-feather"
import Avatar from "@components/avatar"
import { Card, CardBody, CardText, Col, Row } from "reactstrap"
import decorationLeft from "@src/assets/images/elements/decore-left.png"
import decorationRight from "@src/assets/images/elements/decore-right.png"
import { useTranslation } from "react-i18next"
import { useDispatch, useSelector } from "react-redux"
import { getUserData } from "@utils"
import { renderIcon } from "@src/views/apps/menu/store/my_menu"
import { getDataList } from "@src/views/my-info/store/employees"
import { Fragment, useEffect, useState } from "react"

const WidgetBirthDayNow = () => {
  const { t } = useTranslation()
  const user = getUserData()

  const storeEmployees = useSelector((state) => state.employees)
  const dispatch = useDispatch()
  const [lastData, setLastData] = useState(null)

  useEffect(() => {
    const date = new Date()
    date.setUTCHours(0, 0, 0, 0)
    date.setDate(date.getDate() + 1)
    let now = new Date(date).toISOString().split("T")[0]
    const lastDataStr = localStorage.getItem("birthdayNow")
    if (lastDataStr !== null) {
      const lastDataObj = JSON.parse(lastDataStr)
      // console.log(lastDataObj?.[0]?.FetchDate === now)
      if (lastDataObj?.[0]?.FetchDate === now) {
        setLastData(lastDataObj)
      } else {
        dispatch(getDataList({})).then((res) => {
          setLastData(res?.payload?.data)
        })
      }
    } else {
      dispatch(getDataList({})).then((res) => {
        setLastData(res?.payload?.data)
      })
    }
  }, [dispatch, getDataList])

  let colors = ["info", "success"]

  let styles = [
    {
      background:
        "linear-gradient(118deg, rgb(42 27 104), rgb(71 51 133 / 98%))"
    },
    {
      background: "linear-gradient(118deg, #184624, rgb(24 123 145 / 70%))"
    }
  ]

  return lastData?.length === 1 ? (
    <Col md="4">
      <Card
        style={styles[0]}
        className="card-congratulations card-congratulations-non-fit"
      >
        <CardBody className="text-center">
          <img
            className="congratulations-img-left"
            src={decorationLeft}
            alt="decor-left"
          />
          <img
            className="congratulations-img-right"
            src={decorationRight}
            alt="decor-right"
          />
          {!lastData[0]?.avatar ? (
            <Avatar
              initials
              className="shadow"
              color="primary"
              content={lastData[0]?.name || "John Doe"}
              size="lg"
            />
          ) : (
            <Avatar
              img={lastData[0]?.avatar}
              imgWidth={28}
              imgHeight={28}
              size="lg"
              className="shadow mb-50"
              color="primary"
            />
          )}
          {
            <div className="text-center">
              <h2 className="text-white">{lastData[0]?.name},</h2>
              <CardText className="m-auto w-75">
                has <strong>Birthday</strong> today.
              </CardText>
            </div>
          }
        </CardBody>
      </Card>
    </Col>
  ) : lastData?.length > 1 ? (
    <Col md="4">
      {lastData.map((row, i) => (
        <Row>
          <Col md="12">
            <Card
              style={styles?.[i]}
              className="card-congratulations card-congratulations-non-fit"
            >
              <CardBody className="text-center">
                <img
                  style={{
                    opacity: 0.4
                  }}
                  className="congratulations-img-left"
                  src={decorationLeft}
                  alt="decor-left"
                />
                <img
                  style={{
                    opacity: 0.4
                  }}
                  className="congratulations-img-right"
                  src={decorationRight}
                  alt="decor-right"
                />
                {!row?.avatar ? (
                  <Avatar
                    initials
                    className="shadow"
                    color={colors?.[i] ?? "primary"}
                    content={row?.name || "John Doe"}
                    size="lg"
                  />
                ) : (
                  <Avatar
                    img={row?.avatar}
                    imgWidth={28}
                    imgHeight={28}
                    size="lg"
                    className="shadow mb-50"
                    color={colors?.[i] ?? "primary"}
                  />
                )}
                {
                  <div className="text-center">
                    <h2 className="text-white">{row?.name},</h2>
                    <CardText className="m-auto w-75">
                      has <strong>Birthday</strong> today.
                    </CardText>
                  </div>
                }
              </CardBody>
            </Card>
          </Col>
        </Row>
      ))}
    </Col>
  ) : null
}

export default WidgetBirthDayNow
