import Avatar from "@components/avatar"
import UILoader from "@components/ui-loader"
import illustration from "@src/assets/images/illustration/email.svg"
import { Loader } from "@src/shared/CommonComponents"
import { getDataList, postDataWidget } from "@src/shared/dynamic_pages/store"
import "moment-timezone"
import "moment/locale/id"
import { useContext, useEffect, useState } from "react"
import { Calendar, Crosshair, MapPin, Video } from "react-feather"
import { useTranslation } from "react-i18next"
import Moment from "react-moment"
import { useDispatch, useSelector } from "react-redux"
import { useNavigate } from "react-router-dom"
import {
  Alert,
  Badge,
  Button,
  Card,
  CardBody,
  CardHeader,
  CardText,
  CardTitle,
  Col,
  Modal,
  ModalBody,
  ModalFooter,
  ModalHeader,
  Spinner
} from "reactstrap"

import AssignmentIndOutlinedIcon from "@mui/icons-material/AssignmentIndOutlined"
import CastForEducationOutlinedIcon from "@mui/icons-material/CastForEducationOutlined"
import { ThemeColors } from "@src/utility/context/ThemeColors"
import { getDataList as getDataListMyActivity } from "@src/views/my-info/store/my_activities"
import { setDataCache } from "@store/dynamicTempState"
import { selectThemeColors } from "@utils"
import toast from "react-hot-toast"
import Select, { components } from "react-select"
import { Swiper, SwiperSlide } from "swiper/react/swiper-react"

import useJwt from "@src/auth/jwt/useJwt"

const axiosSso = useJwt.ssoAxiosInterceptor
const axiosHR = useJwt.hrAxiosInterceptor

const params = {
  // navigation: true
  pagination: {
    clickable: true
  },
  autoplay: {
    delay: 5500,
    disableOnInteraction: false
  },
  slideToClickedSlide: true
}

const WidgetTrainingAssignedForMe = () => {
  const { t } = useTranslation()
  // const store = useSelector((state) => state[storeName])
  const store = useSelector((state) => state.dynamicStore)
  const dynamicTempState = useSelector((state) => state.dynamic_temp_state)
  const dispatch = useDispatch()
  const navigate = useNavigate()

  const storeName = "TrainingEmployee_assignForMe"
  const endpointName = "TrainingEmployee/assignment"
  const { colors } = useContext(ThemeColors)
  const [loading, setLoading] = useState(true)
  const [loadingPostPut, setLoadingPostPut] = useState(false)
  const [loadingText, setLoadingText] = useState("Please Wait")

  const [loadingGetManager, setLoadingGetManager] = useState(false)
  const [managerData, setManagerData] = useState([])
  const [managerDataSelected, setManagerDataSelected] = useState(null)
  const [isOpen, setIsOpen] = useState(false)
  const toggle = () => {
    setIsOpen(!isOpen)
  }

  const errorToast = (res) => {
    let errorFieldsMsg = ""
    if (res?.payload?.error?.errors) {
      const errorFields = res?.payload?.error?.errors
      for (let errorx in res?.payload?.error?.errors) {
        errorFieldsMsg = `${errorFieldsMsg} - ${errorFields[errorx]}\n`
        // console.log(errorx, errorFields[errorx])
      }
    }
    return toast.error(
      `${res.payload?.error?.message} \n \n ${errorFieldsMsg}`,
      {
        style: {
          padding: "16px",
          color: colors.danger.main,
          border: `1px solid ${colors.danger.main}`
        },
        iconTheme: {
          primary: colors.danger.main
        }
      }
    )
  }

  const postDataToApi = (data) => {
    setLoadingPostPut(true)
    setLoadingText("Sending Training Request to Manager")
    data["storeName"] = storeName
    data["endpointName"] = endpointName
    data["baseUrl"] = "hr"
    data["callback_page"] = window.location.href
    dispatch(postDataWidget(data))
      .then((res) => {
        if (res.meta.requestStatus === "fulfilled") {
          dispatch(
            getDataList({
              storeName: storeName,
              endpointName: endpointName,
              baseUrl: "hr",
              type: "for_me",
              Id_AttendingState: 0
            })
          )
            .then((xxx) => {
              setLoadingPostPut(false)
              toggle()
              toast.success(
                `${t("Successfully Sending Training Request to Manager")}}`,
                {
                  style: {
                    padding: "16px",
                    color: colors.success.main,
                    border: `1px solid ${colors.success.main}`
                  },
                  iconTheme: {
                    primary: colors.success.main
                  }
                }
              )
            })
            .then((xx) => {
              dispatch(
                getDataList({
                  storeName: "TrainingEmployee_assignToMe",
                  endpointName: "TrainingEmployee/assignment",
                  baseUrl: "hr",
                  type: "to_me",
                  Id_AttendingState: 1
                })
              )
              setTimeout(() => {
                dispatch(getDataListMyActivity({}))
              }, 8000)
            })
        } else if (res.meta.requestStatus === "rejected") {
          setLoadingPostPut(false)
          errorToast(res)
        }
      })
      .catch((error) => {
        // console.error(error)
        setLoadingPostPut(false)
        errorToast(error)
      })
  }

  const getManager = async () => {
    toggle()

    const storeInputName = "Id_Manager"
    if (!dynamicTempState.data[storeInputName] && !loadingGetManager) {
      setLoadingGetManager(true)
      const finUrl = "DepartmentManager"
      await axiosHR.get(finUrl).then((res) => {
        // console.log(res)
        const data = res?.data?.data
        setManagerData(data)
        setLoadingGetManager(false)
        dispatch(setDataCache({ [storeInputName]: data }))
      })
    } else {
      setLoadingGetManager(true)
      setManagerData(dynamicTempState.data[storeInputName])
      setLoadingGetManager(false)
    }
  }

  useEffect(() => {
    if (
      !store.pageData[storeName] ||
      (store.pageData[storeName]?.data?.length === 0 &&
        !store.pageData[storeName]?.isLoading)
    ) {
      dispatch(
        getDataList({
          storeName: storeName,
          endpointName: endpointName,
          baseUrl: "hr",
          type: "for_me",
          Id_AttendingState: 0
        })
      )
    }
  }, [])

  const renderAvatarImgIcon = (data) => {
    let componentsInput = {}
    const { Option, SingleValue } = components
    const IconOption = (props) => (
      <Option {...props}>
        <img
          src={props.data.img}
          style={{
            height: "30px",
            width: "30px",
            borderRadius: "10%",
            marginRight: "10px"
          }}
        />
        {props.data.label} <small>({props.data.position})</small>
      </Option>
    )

    const IconSingleValue = (props) => (
      <SingleValue {...props}>
        <img
          src={props.data.img}
          style={{
            height: "30px",
            width: "30px",
            borderRadius: "10%",
            marginRight: "10px"
          }}
        />
        {props.data.label} <small>({props.data.position})</small>
      </SingleValue>
    )

    componentsInput = {
      components: {
        SingleValue: IconSingleValue,
        Option: IconOption
      }
    }

    return componentsInput
  }

  const calendarStrings = {
    lastDay: "[Yesterday]",
    sameDay: "[Today]",
    nextDay: "[Tomorrow]",
    lastWeek: "[last] dddd",
    nextWeek: "dddd",
    sameElse: "L"
  }

  const colorsBadge = [
    "primary",
    "success",
    "danger",
    "warning",
    "info",
    "secondary"
  ]

  const toUpperCaseFilter = (d) => {
    return d.toUpperCase()
  }

  {
    return store.pageData[storeName]?.isLoading ? (
      <Col lg="12" xs="12">
        <Card style={{ minHeight: "200px" }}>
          <CardHeader>
            <div className="d-flex align-items-center">
              <CastForEducationOutlinedIcon className="user-timeline-title-icon me-1" />
              <CardTitle tag="h4">{t("Recommended Training")}</CardTitle>
            </div>
          </CardHeader>
          <UILoader blocking={true} loader={<Loader />}>
            <CardBody></CardBody>
          </UILoader>
        </Card>
      </Col>
    ) : store.pageData[storeName]?.data?.length > 0 ? (
      <Col lg="12" xs="12">
        <Card>
          <CardHeader>
            <div className="d-flex align-items-center">
              <CastForEducationOutlinedIcon className="user-timeline-title-icon me-1" />
              <CardTitle tag="h4">
                {!store.pageData[storeName]?.data[0]?.isManager
                  ? t("Recommended Training For You")
                  : t("Recommended Training For Your Staff")}
              </CardTitle>
              {store.pageData[storeName]?.data?.length > 1 && (
                <Badge className="ms-1" tag="div" color="info" pill>
                  {store.pageData[storeName]?.data?.length}
                </Badge>
              )}
            </div>
          </CardHeader>
          <CardBody className="px-0 pb-0">
            <Swiper {...params}>
              {store.pageData[storeName]?.data.map((row) => {
                return (
                  <SwiperSlide key={`swiper_${row.id}`}>
                    <Card className="card-developer-meetup cursor-pointer">
                      <div className="meetup-img-wrapper rounded-top text-center">
                        <img
                          src={
                            row?.TrainingData?.Image
                              ? row.TrainingData.Image
                              : illustration
                          }
                          height="170"
                        />
                      </div>
                      <CardBody>
                        <div className="meetup-header d-flex align-items-center">
                          <div className="meetup-day">
                            <h6 className="mb-0">
                              <Moment filter={toUpperCaseFilter} format="ddd">
                                {row?.TrainingData?.StartDate}
                              </Moment>
                            </h6>
                            <h3 className="mb-0">
                              <Moment format="DD">
                                {row?.TrainingData?.StartDate}
                              </Moment>
                            </h3>
                          </div>
                          <div className="my-auto text-truncate">
                            <CardTitle tag="h4" className="mb-25">
                              {row?.TrainingData?.Name}
                            </CardTitle>
                            <CardText className="mb-0">
                              {row?.TrainingData?.Description}
                            </CardText>
                          </div>
                        </div>
                        <div className="d-flex">
                          <Avatar
                            color="light-primary"
                            className="rounded me-1"
                            icon={<Calendar size={18} />}
                          />
                          <div>
                            {/* <h6 className="mb-0">Sat, May 25, 2020</h6>
                    <small>10:AM to 6:PM</small> */}
                            <h6 className="mb-0">
                              {
                                <Moment calendar={calendarStrings}>
                                  {row?.TrainingData?.StartDate}
                                </Moment>
                              }
                            </h6>
                            <small>
                              {
                                <Moment format="[Start at] LT">
                                  {row?.TrainingData?.StartDate}
                                </Moment>
                              }
                            </small>
                          </div>
                        </div>
                        {!row?.TrainingData?.IsOnline ? (
                          <div className="d-flex mt-2">
                            <Avatar
                              color="light-primary"
                              className="rounded me-1"
                              icon={<MapPin size={18} />}
                            />
                            <div className="align-items-center align-middle my-auto">
                              {/* <h6 className="mb-0">Central Park</h6> */}
                              <small>{row?.TrainingData?.Address}</small>
                            </div>
                          </div>
                        ) : (
                          <div className="d-flex mt-2">
                            <Avatar
                              color="light-primary"
                              className="rounded me-1"
                              icon={<Video size={18} />}
                            />
                            <div className="align-items-center align-middle my-auto">
                              <h6 className="mb-0">{t("Online Training")}</h6>
                              {row?.TrainingData?.LinkRef && (
                                <small>
                                  {row?.TrainingData?.LinkRef
                                    ? row?.TrainingData?.LinkRef
                                    : "The link will be sent before the event starts"}
                                </small>
                              )}
                            </div>
                          </div>
                        )}
                        {row?.FunctionalPosition && (
                          <div className="d-flex mt-2">
                            <Avatar
                              color="light-primary"
                              className="rounded me-1"
                              icon={<Crosshair size={18} />}
                            />
                            <div className="align-items-center align-middle my-auto">
                              {row.FunctionalPosition.map((rowx, i) => {
                                return (
                                  <Badge
                                    key={`badge_${i}`}
                                    color={`light-${
                                      colorsBadge[i] ?? "primary"
                                    }`}
                                  >
                                    {rowx?.name}
                                  </Badge>
                                )
                              })}
                            </div>
                          </div>
                        )}
                        {/* <AvatarGroup data={data} /> */}
                        {!row?.isManager ? (
                          <Col className="d-grid mt-2" sm="12">
                            <Button.Ripple
                              onClick={getManager}
                              color="gradient-primary"
                            >
                              {t("Follow")}
                            </Button.Ripple>
                            {/* <Collapse isOpen={isOpen}>
                              <Alert color="primary">
                                <div className="alert-body">
                                  <Info size={15} />
                                  <span className="ms-1">
                                    Chupa chups topping bonbon. Jelly-o toffee I
                                    love. Sweet I love wafer I love wafer.
                                  </span>
                                </div>
                              </Alert>

                              <Col className="mt-1" md="12" sm="12">
                                <Label className="form-label">
                                  {t("Please Select Your Manager")}
                                </Label>
                                <Select
                                  theme={selectThemeColors}
                                  className="react-select"
                                  classNamePrefix="select"
                                  defaultValue={colourOptions[0]}
                                  options={colourOptions}
                                  isClearable={false}
                                />
                              </Col>
                            </Collapse> */}
                            <Modal
                              isOpen={isOpen}
                              toggle={toggle}
                              className={"modal-dialog-centered"}
                            >
                              <ModalHeader toggle={toggle}>
                                {t("Manager Approval Required")}
                              </ModalHeader>
                              <ModalBody>
                                <Alert color="primary">
                                  <div className="alert-body">
                                    {t(
                                      "Please select your manager to approve your participation in this training"
                                    )}
                                  </div>
                                </Alert>

                                <Col className="mt-1" md="12" sm="12">
                                  <Select
                                    {...renderAvatarImgIcon(managerData)}
                                    // isDisabled={loadingGetManager}
                                    placeholder={t("Choose Manager")}
                                    isLoading={loadingGetManager}
                                    theme={selectThemeColors}
                                    className="react-select"
                                    classNamePrefix="select"
                                    onChange={setManagerDataSelected}
                                    options={managerData.map((item) => {
                                      return {
                                        label: item.Employee_Name,
                                        value: item.Id_Employee,
                                        img: item?.Employee_PhotoPath,
                                        position: item?.PositionName
                                      }
                                    })}
                                    isClearable={false}
                                  />
                                </Col>
                              </ModalBody>
                              <ModalFooter>
                                {!loadingPostPut ? (
                                  <Button
                                    disabled={!managerDataSelected}
                                    color="primary"
                                    onClick={(e) => {
                                      e.preventDefault()
                                      postDataToApi({
                                        id: row?.Id_Training,
                                        manager_id: managerDataSelected.value
                                      })
                                    }}
                                    outline
                                  >
                                    {t("Apply to Manager")}
                                  </Button>
                                ) : (
                                  <Button.Ripple color="primary">
                                    <Spinner
                                      color="white"
                                      size="sm"
                                      type="grow"
                                    />
                                    <span className="ms-50">{loadingText}</span>
                                  </Button.Ripple>
                                )}
                              </ModalFooter>
                            </Modal>
                          </Col>
                        ) : (
                          <Col className="d-grid mt-2" sm="12">
                            <Button.Ripple
                              onClick={(e) => {
                                navigate(
                                  `/dyn_pages/hr/training/edit/${row.Id_Training}/training_employee`
                                )
                              }}
                              color="gradient-primary"
                            >
                              <AssignmentIndOutlinedIcon size={14} />
                              <span className="align-middle ms-25">
                                {t("Assign to Staff")}
                              </span>
                            </Button.Ripple>
                          </Col>
                        )}
                      </CardBody>
                    </Card>
                  </SwiperSlide>
                )
              })}
            </Swiper>
          </CardBody>
        </Card>
      </Col>
    ) : null
  }
}

export default WidgetTrainingAssignedForMe
