import Avatar from "@components/avatar"
import { ThemeColors } from "@src/utility/context/ThemeColors"
import "@styles/react/libs/charts/apex-charts.scss"
import {
  getAppName,
  getUserData,
  isAdminApp,
  isErpApp,
  urlReplacer,
  restrictedMenuLink
} from "@utils"
import { Fragment, useContext, useEffect, useRef, useState } from "react"
import { AlertCircle, Coffee, X } from "react-feather"
import toast from "react-hot-toast"
import { useTranslation } from "react-i18next"
import { useDispatch, useSelector } from "react-redux"
import {
  Button,
  Card,
  CardBody,
  CardHeader,
  CardTitle,
  Col,
  DropdownItem,
  DropdownMenu,
  DropdownToggle,
  Row,
  UncontrolledDropdown
} from "reactstrap"
import useJwt from "@src/auth/jwt/useJwt"
import Editor from "@monaco-editor/react"
// ** Firebase Notifications
// import { requestForToken, requestNotificationPermission } from "@src/firebase"
import { AbilityContext } from "@src/utility/context/Can"
// widgets
// my-info
import WidgetCardCongratulations from "./WidgetCardCongratulations"
import WidgetLastActivity from "./WidgetLastActivity"
// others
import WidgetAnnouncement from "./widgets/WidgetAnnouncement"
import WidgetBirthDayNow from "./widgets/WidgetBirthDayNow"
import WidgetLeaveNeedApproval from "./widgets/WidgetLeaveNeedApproval"
import WidgetTrainingAssignedForMe from "./widgets/WidgetTrainingAssignedForMe"
import WidgetTrainingAssignedToMe from "./widgets/WidgetTrainingAssignedToMe"
import WidgetTrainingNeedApproval from "./widgets/WidgetTrainingNeedApproval"
import WidgetTrainingTask from "./widgets/WidgetTrainingTask"
import WidgetUsersActivity from "./widgets/WidgetUsersActivity"
import WidgetGeneralEmployee from "./widgets/WidgetGeneralEmployee"
// import Swal from "sweetalert2"
import { renderIcon } from "../../views/apps/menu/store/my_menu"

import {
  setStorePageAttributes,
  getDataList,
  putData
} from "../../shared/dynamic_pages/store"
import {
  setEditModeJson,
  setEditModeJsonAttr,
  setOriginalJsonAttr,
  setChangedJsonAttr,
  setLastState
} from "@store/developer"
import DynamicCard from "../../utility/generateForm/dynamicCard"

import SwiperCore, {
  Autoplay,
  EffectCoverflow,
  EffectCube,
  EffectFade,
  Grid,
  Lazy,
  Navigation,
  Pagination,
  Virtual
} from "swiper"

// ** Styles
import "@styles/react/libs/swiper/swiper.scss"
import { Fullscreen, FullscreenExit, ThumbUp } from "@mui/icons-material"

// ** Init Swiper Functions
SwiperCore.use([
  Navigation,
  Grid,
  Pagination,
  EffectFade,
  EffectCube,
  EffectCoverflow,
  Autoplay,
  Lazy,
  Virtual
])

const ToastContent = ({ x, name, message, isSavingWidget, isError }) => {
  return (
    <div className="d-flex">
      <div className="me-1">
        <Avatar
          size="sm"
          color={!isError ? "success" : "danger"}
          icon={!isSavingWidget ? <Coffee size={12} /> : <ThumbUp size={14} />}
        />
      </div>
      <div className="d-flex flex-column">
        <div className="d-flex justify-content-between">
          <h6>{name}</h6>
          <X
            size={12}
            className="cursor-pointer"
            onClick={() => toast.dismiss(x.id)}
          />
        </div>
        <span>{message}</span>
      </div>
    </div>
  )
}

const Dashboard = () => {
  // ** Context
  const { t } = useTranslation()
  const user = getUserData()
  const appName = getAppName()
  const ability = useContext(AbilityContext)
  const store = useSelector((state) => state.dynamicStore)
  const widgetVisibility = useSelector((state) => state.layout.widgetVisibility)
  const dispatch = useDispatch()
  const [components, setComponents] = useState([])
  const userData = getUserData()
  useEffect(() => {
    if (localStorage.getItem("afterLogin") !== null) {
      toast((x) => (
        <ToastContent
          x={x}
          name={user.name || user.username || "John Doe"}
          message={`${t("You have successfully logged in as an ")}${
            user.role_name || "admin"
          } ${t("to")} ${appName}. ${t(
            "Now you can start to explore. Enjoy!"
          )}`}
        />
      ))
    }

    getUserRoleDashboard()

    return () => {
      localStorage.removeItem("afterLogin")
    }
  }, [])

  const storeDeveloper = useSelector((state) => state.developer)
  const storeName2 = "__app_widget__"
  const endpointName2 = "api/app/app_widget"
  const [isFullscreen, setIsFullscreen] = useState(false)
  const requestFullscreen = () => {
    const element = document.getElementById("card_json_dev")
    // setOldStyleBackgroundColor(element.style.backgroundColor)
    // if (element) {
    //   element.style.backgroundColor = "#1e1e1e"
    // }
    if (element.requestFullscreen) {
      element.requestFullscreen()
    } else if (element.mozRequestFullScreen) {
      element.mozRequestFullScreen()
    } else if (element.webkitRequestFullscreen) {
      element.webkitRequestFullscreen()
    } else if (element.msRequestFullscreen) {
      element.msRequestFullscreen()
    }

    setIsFullscreen(true)
  }

  const exitFullscreen = () => {
    // if (oldStyleBackgroundColor) {
    //   const element = document.getElementById("card_json_dev")
    //   if (element) {
    //     element.style.backgroundColor = oldStyleBackgroundColor
    //   }
    // }

    if (
      document.fullscreenElement ||
      document.mozFullScreenElement ||
      document.webkitFullscreenElement ||
      document.msFullscreenElement
    ) {
      if (document.exitFullscreen) {
        document.exitFullscreen()
      } else if (document.mozCancelFullScreen) {
        document.mozCancelFullScreen()
      } else if (document.webkitExitFullscreen) {
        document.webkitExitFullscreen()
      } else if (document.msExitFullscreen) {
        document.msExitFullscreen()
      }
    }

    setIsFullscreen(false)
  }

  const devModeEnabled =
    storeDeveloper.editMode &&
    ((!window.location.pathname.includes("/dyn_pages/_admin_") &&
      !window.location.pathname.includes("/apps/menu")) ||
      isAdminApp())
  const [editorValid, setEditorValid] = useState(true)
  const [foldAll, setFoldAll] = useState(false)

  const resetDevState = () => {
    dispatch(setOriginalJsonAttr({ storeName: storeName2, pageAttr: null }))
    // setTempJsonAttr(null)
    dispatch(setChangedJsonAttr({ storeName: storeName2, pageAttr: null }))
    setEditorValid(true)
  }

  const initNewModeJson = (pageAttrInit) => {
    let newPageAttr = JSON.stringify(
      pageAttrInit ?? store.pageAttributes[storeName2] ?? [],
      null,
      2
    )
    if (
      !storeDeveloper?.pageAttributesBackup ||
      !storeDeveloper?.pageAttributesBackup?.[storeName2]
    ) {
      // console.log("setOriginalJsonAttr")
      // setOriginalJsonAttr(newPageAttr)
      dispatch(
        setOriginalJsonAttr({ storeName: storeName2, pageAttr: newPageAttr })
      )
    }
    dispatch(
      setEditModeJson({
        storeName: storeName2,
        pageAttr: newPageAttr
      })
    ).then((res) => {
      // console.log(res)
    })
  }

  const editorRef = useRef(null)

  function handleEditorDidMount(editor, monaco) {
    editorRef.current = editor
    const lastLineNumber =
      storeDeveloper?.pageAttributesLastState[storeName2]?.lineNumber
    if (lastLineNumber) {
      editor.revealLineInCenter(lastLineNumber)
      editor.setPosition({ column: 1, lineNumber: lastLineNumber })
    }
    // else {
    //   editor.getAction("editor.foldLevel4").run()
    // }
  }

  const setEditModeJsonState = (e, isDiscard = false) => {
    e.preventDefault()
    // console.log("setEditModeJsonState")
    if (isDiscard) {
      // console.log("discard")
      // console.log(originalJsonAttr.length)
      let originalJsonAttrObj = JSON.parse(
        storeDeveloper.pageAttributesBackup[storeName2] || []
      )

      dispatch(
        setStorePageAttributes({
          storeName: storeName2,
          data: originalJsonAttrObj
        })
      ).then((data) => {
        setTimeout(() => {
          resetDevState()
        }, 50)
        dispatch(setEditModeJson({ storeName: storeName2 })).then((res) => {})

        const queryParams = new URLSearchParams(window.location.search)
        const dev_mode_json_qs = queryParams.get("dev_mode_json")
        if (dev_mode_json_qs || dev_mode_json_qs === "true") {
          queryParams.delete("dev_mode_json")
          navigate({ search: queryParams.toString() })
        }
      })
    } else {
      initNewModeJson()
    }

    if (isFullscreen) {
      exitFullscreen()
    }
  }

  function handleEditorChange(value, event) {
    // console.log("handleEditorChange")
    // console.log(editorRef.current)
    let editor = editorRef.current
    let lastState = {
      lineNumber: editor.getPosition().lineNumber
    }
    dispatch(setLastState({ storeName: storeName2, lastState })).then(
      (state) => {
        // console.log(state)
      }
    )

    setEditorValid(true)
    // setTempJsonAttr(value)
    dispatch(setChangedJsonAttr({ storeName: storeName2, pageAttr: value }))
    dispatch(
      setEditModeJsonAttr({
        storeName: storeName2,
        pageAttr: value
      })
    ).then((res) => {
      // console.log(res)
    })
  }

  function handleEditorValidation(markers) {
    setEditorValid(markers?.length === 0)
  }

  const saveJson = async (e) => {
    const changedJson = JSON.parse(
      storeDeveloper.pageAttributesChanged[storeName2] ??
        storeDeveloper.pageAttributes[storeName2]
    )
    // console.log(changedJson)
    dispatch(
      setStorePageAttributes({ storeName: storeName2, data: changedJson })
    ).then((result) => {
      setWidgetComponents(changedJson)
      setTimeout(() => {
        setEditModeJsonState(e)
        if (isFullscreen) {
          exitFullscreen()
        }
      }, 100)
      resetDevState()
    })

    // dispatch(
    //   putData({
    //     storeName: storeName2,
    //     endpointName: endpointName2,
    //     baseUrl: "sso"
    //   })
    // ).then((res) => {
    //   console.log(res)
    // })

    const axiosSso = useJwt.ssoAxiosInterceptor
    await axiosSso({
      method: "put",
      url: endpointName2,
      data: {
        dashboardWidget: JSON.stringify(changedJson)
      }
    })
      .then((res) => {
        console.log(res)
        if (res?.status === 200) {
          toast((x) => (
            <ToastContent
              x={{ id: 2222 }}
              name={"Dashboard Widget Saved"}
              message={`${t(
                "Dashboard Widget Successfully Saved To Database"
              )}`}
              isSavingWidget={true}
            />
          ))
        } else {
          toast((x) => (
            <ToastContent
              x={{ id: 2222 }}
              name={"Widgets Failed to Save"}
              message={`${t("Dashboard Widgets Failed to Save")}`}
              isSavingWidget={true}
              isError={true}
            />
          ))
        }
      })
      .catch((err) => {
        console.error(err)
        toast((x) => (
          <ToastContent
            x={{ id: 2222 }}
            name={"Widgets Failed to Save"}
            message={`${t("Dashboard Widgets Failed to Save")}`}
            isSavingWidget={true}
            isError={true}
          />
        ))
      })
  }

  const editorGoTo = (e, item) => {
    e.preventDefault()
    const editor = editorRef.current
    const lineData = item.value
    editor.setScrollPosition({ scrollTop: 0 })
    editor.setPosition({
      column: lineData?.startColumn,
      lineNumber: lineData?.startLineNumber
    })
    // editor.revealLine(lineData?.startLineNumber)
    editor.revealLineInCenter(lineData?.startLineNumber)
    // editor.trigger("unfold", "editor.unfoldAll")
  }

  const foldEvent = (e, value) => {
    e.preventDefault()
    const editor = editorRef.current
    editor.setScrollPosition({ scrollTop: 0 })
    editor.setPosition({
      column: 0,
      lineNumber: 0
    })
    editor.revealLine(0)
    if (value) {
      editor.trigger("unfold", "editor.unfoldAll")
      setTimeout(() => {
        editor.getAction("editor.foldLevel2").run()
      }, 30)
    } else {
      editor.trigger("unfold", "editor.unfoldAll")
    }
    setFoldAll(value)
  }

  const previewJson = async (e) => {
    const changedJson = JSON.parse(
      storeDeveloper.pageAttributesChanged[storeName2]
    )

    dispatch(
      setStorePageAttributes({ storeName: storeName2, data: changedJson })
    ).then((result) => {
      setWidgetComponents(changedJson)

      setTimeout(() => {
        setEditModeJsonState(e)
        if (isFullscreen) {
          exitFullscreen()
        }
      }, 100)
    })
  }

  const saveJson2 = async (changedJson) => {
    // console.log(changedJson)
    dispatch(setStorePageAttributes({ storeName, data: changedJson })).then(
      (result) => {
        // console.log(result)
        setPageAttr(changedJson)
        setShowModalDevState(!showModalDevState)
        setTimeout(() => {
          renderPage()
        }, 50)

        // setTimeout(() => {
        //   resetAllData()
        // }, 250)
      }
    )

    let currentMenuData = storeMenu?.dataMyMenu?.find(
      (item) => item?.routePath === window.location.pathname
    )

    const axiosSso = useJwt.ssoAxiosInterceptor
    await axiosSso({
      method: "put",
      url: `/api/menu/${currentMenuData.id}`,
      data: {
        pageAttr: changedJson
      }
    }).then((res) => {
      // console.log(res)
    })
  }

  const setWidgetComponents = (dataResp) => {
    setComponents([])
    if (dataResp?.length > 0) {
      dataResp?.map((item, index) => {
        const storeNameFix =
          item?.chart_id ??
          item?.cardId ??
          item?.pageId ??
          item?.crudOpt?.pageId ??
          `__app_dashboard_widget__${index}`
        return setComponentCard(storeNameFix, item)
      })
    }
  }

  useEffect(() => {
    if (
      !store.pageData[storeName2] &&
      !store.pageAttributes[storeName2] &&
      !store.pageData[storeName2]?.isLoading
    ) {
      dispatch(
        getDataList({
          storeName: storeName2,
          endpointName: endpointName2,
          baseUrl: "sso"
        })
      ).then((res) => {
        const dataResp = res?.payload?.data
        setWidgetComponents(dataResp)
        dispatch(
          setStorePageAttributes({
            storeName: storeName2,
            data: dataResp ?? []
          })
        ).then((attr) => {
          // console.log(attr)
        })
      })
    } else {
      const dataResp =
        store.pageAttributes[storeName2] ?? store.pageData[storeName2]?.data
      setWidgetComponents(dataResp)
    }
  }, [])

  async function setComponentCard(storeNameFix, data) {
    ////SET COMPONENT CARD
    const newComponents = []
    const newComponent = (
      <DynamicCard
        key={`dynamicCard_home_${storeNameFix}`}
        dynamicCard={data?.options?.data?.dynamicCard}
        storeName={storeNameFix}
        pageAttr={data}
        mainDashboard={true}
      ></DynamicCard>
    )
    newComponents.push(newComponent)
    setComponents((prevComponents) => [...prevComponents, ...newComponents])
  }

  async function getPageAttributes(path) {
    const timestamp = new Date().getTime()
    const storeNameFix = path.split("/")?.pop()
    if (!store.pageAttributes[storeNameFix]) {
      if (path.includes("_admin_")) {
        await import(`@src/views/admin_pages/${pageId}.json`)
          .then((res) => {
            try {
              return res.default
            } catch (error) {
              console.error(error)
              throw new Error("Bad response from server")
            }
          })
          .then((data) => {
            const newData = urlReplacer(data)
            dispatch(
              setStorePageAttributes({ storeName: storeNameFix, data: newData })
            )
            setComponentCard(storeNameFix, data)
            return newData
          })
          .catch((error) => {
            console.error(error)
          })
      } else {
        let url = `${window.location.origin}/pageAttributes/${path}.json?timestamp=${timestamp}`
        await fetch(url)
          .then(function (res) {
            try {
              return res.json()
            } catch (error) {
              console.error(error)
              throw new Error("Bad response from server")
            }
          })
          .then((data) => {
            const newData = urlReplacer(data)
            dispatch(
              setStorePageAttributes({ storeName: storeNameFix, data: newData })
            )
            setComponentCard(storeNameFix, data)
            return data
          })
          .catch((error) => {
            console.error(error)
          })
      }
    } else {
      setComponentCard(storeNameFix, store.pageAttributes[storeNameFix])
    }
  }

  async function getUserRoleDashboard() {
    if (user?.dashboard) {
      return user.dashboard?.map(async (item) => {
        await getPageAttributes(item)
      })
    }
  }

  return (
    <div id="dashboard-analytics">
      {!storeDeveloper.pageAttributes[storeName2] ? (
        <Fragment>
          {devModeEnabled && (
            <div className="d-none d-md-flex mb-1 justify-content-end">
              {storeDeveloper.pageAttributesBackup[storeName2] && (
                <Button color="flat-warning" className="btn-sm" disabled>
                  {renderIcon("material:preview", "align-middle", 12)}
                  <span className="align-middle ms-25">{t("Preview")}</span>
                </Button>
              )}
              <Button
                onClick={(e) => setEditModeJsonState(e)}
                color="relief-dark"
                className="btn-sm btn-dev-mode-json"
              >
                {renderIcon("material:data_object", "align-middle", 12)}
                <span className="align-middle ms-25">JSON (Widgets)</span>
              </Button>
            </div>
          )}

          <Row className="match-height">
            {(widgetVisibility?.greeting === true || !widgetVisibility) && (
              <Col lg="6" sm="12">
                <WidgetCardCongratulations />
              </Col>
            )}

            {(widgetVisibility?.announcements === true ||
              !widgetVisibility) && <WidgetAnnouncement lg="6" sm="12" />}

            {/* DYNAMIC DASHBOARD */}
            {components}

            {!isAdminApp() && isErpApp() && <WidgetBirthDayNow />}

            {/* {user.id_employee && <WidgetGeneralEmployee />} */}

            {((!isAdminApp() &&
              isErpApp() &&
              ability.can("read", "erp_hr_manager") &&
              user.id_employee) ||
              (!isAdminApp() && user.isManager)) && (
              <WidgetTrainingNeedApproval />
            )}

            {((ability.can("read", "erp_hr_manager") && user.id_employee) ||
              user.isManager) &&
              !isAdminApp() &&
              isErpApp() && <WidgetLeaveNeedApproval />}

            <Col>
              <WidgetLastActivity />
            </Col>
            {(ability.can("read", "users_management") ||
              ability.can("read", "erp_hr_manager") ||
              user.isManager) && (
              <Col>
                <WidgetUsersActivity hide_me={1} />
              </Col>
            )}
          </Row>
        </Fragment>
      ) : (
        <Fragment>
          <Card
            style={{
              backgroundColor: "#1e1e1e",
              color: "#FEFBF8"
            }}
            title={t("JSON Editor For Dashboard Widgets")}
            id="card_json_dev"
          >
            <CardHeader className="pt-1 pb-1 justify-content-beetween">
              <CardTitle>{t("JSON Editor For Dashboard Widgets")}</CardTitle>

              <div className="justify-content-end">
                {foldAll ? (
                  <Button.Ripple
                    onClick={(e) => foldEvent(e, false)}
                    color="relief-dark"
                    className="btn-sm ms-50"
                  >
                    {renderIcon("material:unfold_less", "align-middle", 12)}
                    <span className="align-middle ms-25">{t("Unfold")}</span>
                  </Button.Ripple>
                ) : (
                  <Button.Ripple
                    onClick={(e) => foldEvent(e, true)}
                    color="relief-dark"
                    className="btn-sm ms-50"
                  >
                    {renderIcon("material:unfold_more", "align-middle", 12)}
                    <span className="align-middle ms-25">{t("Fold All")}</span>
                  </Button.Ripple>
                )}

                <Button.Ripple
                  style={{
                    padding: "0.2rem"
                  }}
                  onClick={(e) =>
                    !isFullscreen ? requestFullscreen() : exitFullscreen()
                  }
                  className="btn-icon btn-sm ms-50"
                  color="relief-dark"
                >
                  {!isFullscreen ? (
                    <Fullscreen size={13} />
                  ) : (
                    <FullscreenExit size={13} />
                  )}
                </Button.Ripple>

                {!storeDeveloper.pageAttributes[storeName2] ? (
                  <Button.Ripple
                    onClick={(e) => setEditModeJsonState(e)}
                    color="relief-dark"
                    className="btn-sm btn-icon btn-dev-mode-json"
                  >
                    {renderIcon("material:data_object", "align-middle", 12)}
                    <span className="align-middle ms-25">JSON</span>
                  </Button.Ripple>
                ) : (
                  <Fragment>
                    <Button.Ripple
                      onClick={(e) => setEditModeJsonState(e, true)}
                      color="relief-dark"
                      className="btn-sm btn-icon ms-50"
                    >
                      {renderIcon("material:close", "align-middle", 12)}
                      <span className="align-middle ms-25">{t("Cancel")}</span>
                    </Button.Ripple>
                    {storeDeveloper.pageAttributesChanged[storeName2] !==
                      storeDeveloper.pageAttributesBackup[storeName2] &&
                      storeDeveloper.pageAttributesChanged[storeName2] && (
                        <Button.Ripple
                          disabled={!editorValid}
                          onClick={(e) => previewJson(e)}
                          color="relief-warning"
                          className="btn-sm ms-50"
                        >
                          {renderIcon("material:preview", "align-middle", 12)}
                          <span className="align-middle ms-25">
                            {t("Preview")}
                          </span>
                        </Button.Ripple>
                      )}
                    {((restrictedMenuLink.includes(window.location.pathname) &&
                      [4, 6, 21].includes(userData.id)) ||
                      !restrictedMenuLink.includes(
                        window.location.pathname
                      )) && (
                      <Button.Ripple
                        disabled={!editorValid}
                        onClick={(e) => saveJson(e)}
                        color="relief-primary"
                        className="btn-sm ms-50"
                      >
                        {renderIcon("material:save", "align-middle", 12)}
                        <span className="align-middle ms-25">{t("Save")}</span>
                      </Button.Ripple>
                    )}
                  </Fragment>
                )}
              </div>
            </CardHeader>
            <CardBody>
              {/* <div
                id="json-diagram-dev"
                className="justify-content-center json-diagram-dev"
              >
                <ul>
                  <JsonDiagramDev
                    data={JSON.parse(
                      storeDeveloper.pageAttributesChanged[storeName] ??
                        storeDeveloper.pageAttributes[storeName]
                    )}
                    name="page"
                  />
                </ul>
              </div> */}
              <Editor
                height={!isFullscreen ? "75vh" : "100%"}
                language="json"
                theme="vs-dark"
                defaultValue={storeDeveloper.pageAttributesChanged[storeName2]}
                value={
                  storeDeveloper.pageAttributesChanged[storeName2] ??
                  storeDeveloper.pageAttributes[storeName2]
                }
                onChange={handleEditorChange}
                onValidate={handleEditorValidation}
                options={{
                  inlineSuggest: true,
                  fontSize: "13px",
                  formatOnType: true,
                  formatOnPaste: true,
                  autoClosingBrackets: true,
                  minimap: { scale: 25, maxColumn: 50 },
                  showFoldingControls: "always",
                  wordWrap: true
                }}
                // beforeMount={handleEditorWillMount}
                onMount={handleEditorDidMount}
              />
            </CardBody>
          </Card>
        </Fragment>
      )}
    </div>
  )
}

export default Dashboard
